@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: white;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.workload__filter {
  width: 100%;
  display: flex;
  padding: 50px 50px 24px;
}
@media (max-width: 767px) {
  .workload__filter {
    flex-flow: column;
    padding: 20px;
  }
}

.workload__filter-functional {
  width: 225px;
  background: linear-gradient(130.43deg, rgba(203, 252, 255, 0.54) 2.85%, rgba(178, 250, 255, 0.54) 48.62%, rgba(47, 222, 233, 0.56) 93.51%);
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(6px);
  border-radius: 5px;
  margin: 0 53px 0 0;
}
@media (max-width: 767px) {
  .workload__filter-functional {
    width: 100%;
    margin: 0 0 30px;
  }
}

.workload__filter-functional-by-time {
  width: 100%;
  height: 100%;
}

.workload__filter-functional-by-time-list {
  height: 100%;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  border-radius: 5px;
  padding: 0;
  margin: 0;
}

.workload__filter-functional-by-time-button {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  border: none;
  border-radius: 5px;
  padding: 21px 15px 15px 24px;
}

.workload__filter-functional-by-time-button--active {
  color: #ffffff;
  background: linear-gradient(84.57deg, #089ACE 9.72%, #22C4CE 63.98%, rgba(5, 168, 90, 0.72) 112.86%);
  border-radius: 5px;
}

.workload__filter-statistics {
  width: 460px;
  color: #666666;
  background: linear-gradient(94.27deg, rgba(212, 252, 255, 0.76) 3.47%, rgba(255, 255, 255, 0) 56.27%, rgba(212, 252, 255, 0.83) 98.93%);
  filter: drop-shadow(0px 4px 6px rgba(0, 0, 0, 0.15));
  backdrop-filter: blur(4px);
  border-radius: 5px;
  padding: 16px 26px 15px 32px;
  margin: 0 43px 0 0;
}
@media (max-width: 767px) {
  .workload__filter-statistics {
    width: 100%;
    padding: 10px;
    margin: 0 0 30px;
  }
}

.workload__filter-statistics-wrapper {
  width: 100%;
  height: 100%;
}

.workload__filter-statistics-table {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.workload__filter-statistics-table-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 0 0 7px;
  margin: 0 0 12px;
}
.workload__filter-statistics-table-row::before {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  bottom: 0;
  background: #666666;
}
.workload__filter-statistics-table-row:last-child {
  margin: 0;
}

.workload__filter-statistics-table-header-cell {
  width: 60%;
  font-size: 16px;
  line-height: 18.75px;
  font-weight: 400;
  text-align: left;
  padding: 0;
}

.workload__filter-statistics-table-description-cell {
  width: 40%;
  text-align: center;
  padding: 0;
}

.workload__filter-statistics-table-description-cell--quantity,
.workload__filter-statistics-table-description-cell--speed {
  font-size: 24px;
  line-height: 28.13px;
  font-weight: 700;
  color: #AA0000;
}

.workload__filter-statistics-table-description-cell--speed-optimal {
  font-size: 24px;
  line-height: 28.13px;
  font-weight: 700;
  color: #339B00;
}

.workload__filter-average {
  width: 219px;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  color: #666666;
  background: linear-gradient(94.27deg, rgba(212, 252, 255, 0.96) 3.47%, rgba(255, 255, 255, 0) 56.27%, rgba(212, 252, 255, 0.83) 98.93%);
  filter: drop-shadow(0px 4px 6px rgba(0, 0, 0, 0.15));
  backdrop-filter: blur(6px);
  border-radius: 5px;
  padding: 12px 0 32px;
}
@media (max-width: 767px) {
  .workload__filter-average {
    width: 100%;
  }
}

.workload__filter-average-description {
  margin: 0 0 19px;
}

.workload__filter-average-information {
  width: 102px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.workload__filter-average-information-image {
  width: 66px;
  height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(158.2deg, rgba(255, 14, 14, 0.61) 12.91%, rgba(170, 0, 0, 0.91) 91.65%);
  box-shadow: 0px 4px 6px rgba(102, 102, 102, 0.17);
  backdrop-filter: blur(6px);
  border-radius: 50%;
}

.workload__filter-average-information-value {
  font-size: 48px;
  line-height: 56.25px;
  font-weight: 900;
  color: #AA0000;
}

.chart-axis-y-title,
.chart-axis-x-title {
  font-size: 16px;
  line-height: 18.75px;
  font-weight: 500;
  color: #00A6E3;
  padding: 0 50px;
}
@media (max-width: 767px) {
  .chart-axis-y-title,
.chart-axis-x-title {
    padding: 0 20px;
    margin: 0;
  }
}

.chart-axis-x-title {
  text-align: right;
}
@media (max-width: 767px) {
  .chart-axis-x-title {
    padding: 0 20px 50px;
  }
}

g.tooltips {
  width: 175px !important;
  height: 175px !important;
  border-radius: 50%;
}

rect#chart-tooltip {
  width: 175px !important;
  height: 175px !important;
  border-radius: 50% !important;
}

text#chart-tooltip-label-text {
  display: block;
  text-align: center;
}

.ct-point {
  stroke-width: 11px;
  cursor: pointer;
}

svg#myChart {
  opacity: 0;
}

.balloon {
  width: 76px;
  height: 97px;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 0px;
  left: 12px;
  background: url("../img/balloon-main-background.png") no-repeat center/cover;
  opacity: 0;
  margin: 15px 0 0;
  z-index: 10;
}

.balloon__information {
  width: 66px;
  height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("../img/balloon-background.png") no-repeat center/cover;
  margin: 4px 0 0;
}

.balloon__information-description {
  font-size: 12px;
  line-height: 16.41px;
  font-weight: 300;
  text-align: center;
  display: block;
  margin: 0;
}

.balloon__information-value {
  font-size: 24px;
  line-height: 28.13px;
  font-weight: 900;
  text-align: center;
}

span.ct-label {
  font-size: 20px;
}

.ct-perfect-fourth {
  height: 500px;
}